import { ChangeEvent, useCallback, useState } from "react"


const useForm = () =>{
    const [states, setStates] = useState({} as any)


    const handleChange = useCallback((value: string, state:string) =>{
        setStates((old:any) => ({
            ...old,
            [state]:value
        }))
    }, [states])


    const addNewState = useCallback(({state, placeholder, name, id} : {state:string, placeholder?:string, name?: string, id?:string }) => {
        return {
            value: states[state] || '',
            onChange: (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => handleChange(e.target.value, state),
            placeholder,
            name,
            id
        }
    }, [states])

    return { addNewState, setStates, states }
}
export default useForm